        <button type="button" :class="['btn', room.isRented()? 'btn-success': 'btn-primary', 'btn-sm', 'daterange']">
          <i class="fa fa-calendar"></i>
        </button>
      </div>
    </div>
    <div class="box-body">
      <div class="info-row">
        <div class="money-box">
          <div class="money-row">
            <div class="money-title"><i class="fa fa-money"></i> </div><div class="money-amount">{{room.getContract()}}</div>
          </div>
          <div class="money-row">
            <div class="money-title"><i class="fa fa-money"></i> </div><div class="money-amount">{{room.getRent()}} / </div>
          </div>
          <div class="money-row">
            <div class="money-title"><i class="fa fa-money"></i> </div><div class="money-amount">{{room.getExpenses()}}</div>
          </div>
        </div>
        <div class="status-box">
          <!-- <div class="badge bg-blue" v-if="room.isReserved()">
            
          </div> -->
          <div class="badge bg-green" v-if="room.isRented()">
            
          </div>
          <div class="badge bg-red" v-if="room.isUnpaid()">
            
          </div>
        </div>
      </div>
      <div class="resident-row">
        <div class="info-row">
          <div class="resident-box">
            <div class="resident-title"><i class="fa fa-user"></i> </div><div class="resident-content"></div>
          </div>
          <div class="resident-box">
            <div class="resident-title"><i class="fa fa-phone"></i> </div><div class="resident-content">08011112222</div>
          </div>
        </div>
        <div class="info-row">
          <div class="resident-note">
            <div class="resident-title"><i class="fa fa-sticky-note"></i>  </div><div class="resident-content"></div>
          </div>
        </div>
      </div>
    </div>
    <div class="box-footer no-padding">
      <div class="room-button">
        
      </div>
      <div class="room-button">
        
      </div>
      <div class="room-button">
        
      </div>
    </div>
  </div>
</template>

<script>
  import CONST from '@/store/const.js'
  import manager from '@/store/manager.js'
  import utils from '@/tool/utils.js'

  export default {
    props: ['manager', 'room'],
    mounted() {

    }
  }
</script>

<style scoped>
  .room-box {
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.4);
  }
  .box-header {
    font-size: 20px;
  }
  .box-header span {
    font-size: 16px;
  }
  .info-row {
    display: flex;
    align-items: center;
  }
  .money-box {
    width: calc(70%);
  }
  .money-row {
    margin: 10px;
    display: flex;
  }
  .money-title {
    width: 80px;
  }
  .status-box {
    width: calc(30%);
    height: 100%;
  }
  .badge {
    margin: 5px;
  }
  .resident-row {
    padding: 10px 5px 10px 5px;
    border: 1px solid #00a65a;
    border-radius: 5px;
  }
  .resident-box {
    margin: 5px;
    width: calc(50%);
    overflow: hidden;
  }
  .resident-title {
    width: 80px;
    float: left;
  }
  .resident-content {
    float: left;
    width: calc(100% - 80px);
  }
  .resident-note {
    margin: 5px;
    overflow: hidden;
  }
  .box-footer {
    display: flex;
    justify-content: center;
  }
  .room-button {
    color: #333;
    font-size: 15px;
    padding: 15px;
    cursor: pointer;
    border-left: 1px solid #eee;
    border-right: 1px solid #eee;
    margin-left: 5px;
    margin-right: 5px;
  }
  .room-button:hover {
    box-shadow:inset 0 1px 5px rgba(0, 0, 0, 0.5);
  }
</style>



